  width: 64px;
  height: 64px;
  background-color: #01e4db;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  z-index: 10;
  transition: background-color 0.3s ease;
}
#to-top-btn svg {
  width: 40px;
