@keyframes slide-down {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slide-up {
  0% {
    opacity: 0;
    transform: translateY(200px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slide-right {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slide-left {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slide-right-down {
  0% {
    opacity: 0;
    transform: translate(-100px, -100px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes slide-left-down {
  0% {
    opacity: 0;
    transform: translate(100px, -100px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes slide-right-up {
  0% {
    opacity: 0;
    transform: translate(-100px, 100px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes slide-left-up {
  0% {
    opacity: 0;
    transform: translate(100px, 100px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes grow {
  0% {
    opacity: 0;
    top: calc(100% - 20px);
  }
  10% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    top: 0;
  }
}
@keyframes bg-grow {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes chart-slide-right {
  0% {
    transform: translateX(-1200px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes chart-slide-down {
  0% {
    transform: translateY(-600px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes chart-fade-in {
  0% {
    opacity: 0;
    transform: translateY(200px);
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes chart-stretch {
  0% {
    height: 0;
  }
  100% {
    height: 151px;
  }
}
@keyframes chart-stretch-mob {
  0% {
    width: 0;
  }
  100% {
    width: 40px;
  }
}
@keyframes stretch-right {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
@keyframes dash {
  from {
    stroke-dashoffset: 2000;
  }
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes border-color-1 {
  from {
    border-color: transparent;
  }
  to {
    border-color: #01e4db;
  }
}
@keyframes border-color-2 {
  from {
    border-color: rgba(255, 255, 255, 0);
  }
  to {
    border-color: #b58426;
  }
}
@keyframes draw-line {
  100% {
    stroke-dashoffset: 0;
    opacity: 1;
  }
}
@keyframes fade-in {
  100% {
    opacity: 1;
  }
}
@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slide-down-fade-in {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slide-right-fade-in {
  0% {
    opacity: 0;
    transform: translateX(-10px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
